<script>
    import {getCid,setCid,getUserInfo} from './service.js';
    import {baseIp} from './config.js';
	export default {
		data(){
			return{
				version:"",
				ciid:"",
			}
		},
		onLaunch(){
			// #ifdef APP-PLUS
				this.setIgexin();
				this.handleUpload();
			
				this.version = plus.runtime.version;
				plus.screen.lockOrientation("portrait-primary")
				var pinf = plus.push.getClientInfo();  
				var cid = pinf.clientid;//客户端标识 
				this.ciid = cid
				setCid(cid);
				if(getUserInfo()){
					if(getUserInfo().userName){
						this.setCiid();
					}
				}
			
				let _handlePush = function(msg) {  
					// TODO  
					var payload = msg.payload;
					
					var testArr = payload.split(',');
					if(payload){
						// console.log(testArr)
						var doc_id = testArr[3].substr(3),
							acceptid = testArr[4].substr(9),
							pdid = testArr[1].substr(5),
							processinstanceid =  testArr[5].substr(18),
							tiid = testArr[0].substr(5),
							deptId = testArr[6].substr(7),
							flowType = testArr[7].substr(9);
						var info = `acceptid=${acceptid}&doc_id=${doc_id}&pdid=${pdid}&piid=${processinstanceid}&tiid=${tiid}&deptId=${deptId}`
						// console.log(info)
						if(flowType == "08"){
							setTimeout(()=>{
								uni.navigateTo({
									url:`/pages/application/investigation6/investigation6?${info}`,
								})
							},1000)
							
						}else if(flowType == "06"){	//请假
							setTimeout(()=>{
								uni.navigateTo({
									url:`/pages/application/investigation7/investigation7?${info}`,
								})
							},1000)
							
						}else if(flowType == "07"){	//用车
							setTimeout(()=>{
								uni.navigateTo({
									url:`/pages/application/investigation8/investigation8?${info}`,
								})
							},1000)
						}else if(flowType == "05"){	//
							setTimeout(()=>{
								uni.navigateTo({
									url:`/pages/application/investigation9/investigation9?${info}`,
								})
							},1000)
						}
					}
				};
				//监听系统通知栏消息点击事件  
				plus.push.addEventListener('click', _handlePush, false);  
				//监听接收透传消息事件  
				plus.push.addEventListener('receive', _handlePush, false);  
							
			// #endif
		},
		onShow(){
			
		},
		methods:{
			// #ifdef APP-PLUS
				//开启推送
				 setIgexin(){
					uni.getProvider({
						service: 'push',
						success: function (res) {
							// console.log(res.provider)

							// 个推的名称为 igexin
							if (~res.provider.indexOf('igexin')) {
								uni.subscribePush({
									provider: 'igexin',
									success: function (res) {
										// console.log('success:' + JSON.stringify(res));
									}
								});
							}
						}
					});
				},
			
				// 检查更新
				handleUpload(){
					var _this=this;  
					var AndroidUrl = `http://${baseIp()}/app/ams.apk`;
					uni.request({  
					    url: `http://${baseIp()}/app/version.json`,
					    success: res => {
							var wgtVer = (res.data.nhams).split('.').join('');
							var thisver = (plus.runtime.version).split('.').join('');
					        if(wgtVer != thisver){
								if(plus.os.name === 'Android'){
									uni.showModal({
										title: '更新提示',
										content: res.data.msg ? res.data.msg : '是否选择更新',
										success: (showResult) => {
											if (showResult.confirm) {
												plus.runtime.openURL(AndroidUrl);
											}
										}
									});
								}
					        }
							//大版本升级
					    } 
					});  
				},
				setCiid(){
					var url = `http://${baseIp()}/ams/system/distribute.htm?module=push_YD&userId=${getUserInfo().userName}&clientId=${this.ciid}`;
					// console.log(url)
					uni.request({
						url,
						success: (res) => {
							let data = res.data;
							// console.log(data)
						},
						fail:(err)=>{
							console.log(err)
						}
					})
				},
			// #endif
		},
	}
</script>

<style>
	@import './common/uni.css';
	@import url("static/css/reset.css");
	@import url("static/font/iconfont.css");
	uni-page-body, uni-page-refresh{height: 100%;min-height: 100%;}
	
	.iconfont{color: rgb(56, 147, 243);}
	.gbg{background-color: rgb(255, 128, 31);color: #fff;}
	.uni-btn-icon{display: block;margin-right: 12px;}
	/* uni-page-head[uni-page-head-type=default]~uni-page-wrapper{height: calc(100% - 44px)} */
	
</style>
